import type React from "react"

interface AlertProps {
  variant?: "default" | "destructive" | "success"
  children: React.ReactNode
}

export function Alert({ variant = "default", children }: AlertProps) {
  const variantStyles = {
    default: "bg-blue-50 border border-blue-200 text-blue-800",
    destructive: "bg-red-50 border border-red-200 text-red-800",
    success: "bg-green-50 border border-green-200 text-green-800",
  }

  return (
    <div className={`p-4 rounded-lg ${variantStyles[variant]}`} role="alert" aria-live="polite">
      {children}
    </div>
  )
}
